.page-loader-wrapper {
    z-index: 99999999;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;    
    overflow: hidden;
    @extend .align-center;
    background: $body-color;

    .loader {
        margin: 0 auto;
        width: 70px;
        height: 50px;
        text-align: center;
        font-size: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translateY(-50%) translateX(-50%);
        
        > div {
            height: 100%;
            width: 8px;
            display: inline-block;
            margin-left: 2px;
            -webkit-animation: delay 0.9s infinite ease-in-out;
            animation: delay 0.9s infinite ease-in-out;
        }
        
        .bar1{
            background-color: #fc3c5f ;
        }
        .bar2{
            background-color: #993cfc;
            -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
        }
        .bar3{
            background-color: #3ca9fc;
            -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
        }
        .bar4{
            background-color: #3cfc5f;
            -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
        }
        .bar5{
            background-color: #fcdf3c;
            -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
        }
    }
    
    
    @-webkit-keyframes delay {
        0%, 40%, 100% { -webkit-transform: scaleY(0.05) }  
        20% { -webkit-transform: scaleY(1.0) }
    }
    
    @keyframes delay {
        0%, 40%, 100% { 
        transform: scaleY(0.05);
        -webkit-transform: scaleY(0.05);
        }  20% { 
        transform: scaleY(1.0);
        -webkit-transform: scaleY(1.0);
        }
    }
}


.setting-list{	
	li{
        padding: 8px 0;
        display: flex;
        align-items: center;
        > span{
            @extend .m-l-10;
        }
	}
}
.chat-widget {
    @extend .padding-0;
    li {
        list-style: none;
        @extend .m-b-30;
        width: 100%;
        
        .avtar-pic{
            margin: 0 !important;
            vertical-align: top;
            float: left;
        }
		
        &.left {

            img {
                float: left;
            }
            .chat-info{
				@extend .m-l-20;							
				@extend .align-left;
                background: rgba($white, 0.03);
                border-radius: 0 10px 10px 10px;
                float: left;

				&:before{					
					border-top: 0 solid transparent;
					border-bottom: 10px solid transparent;
					border-right: 10px solid rgba($white, 0.03);
					left: -10px;
					top: 0;
				}
			}
        }
        &.right {
            @extend .align-right;
            float: right;

			img {
                float: right;
            }
			
            .chat-info {
				@include inline-block;
				@extend .m-r-20;
                @extend .align-right;
                color: $gray-500;
                border-radius: 10px 0 10px 10px;                

				&:before{					
					border-top: 0 solid transparent;
					border-bottom: 10px solid transparent;					
					right: -10px;
					top: 0;
				}
            }
		}		
		.chat-info{
			@include inline-block;
			width: calc(100% - 60px);			
			padding: 10px;
			position: relative;

			&:before{
				width: 0;
				height: 0;				
				content: '';
				position: absolute;
				top: 12px;
			}
		}		
        .datetime {
            font-size: 12px;
            color: $gray-400;
        }
        .message {
            @include display-block;
        }
        img {
            height: 40px;
            width: 40px;
        }
    }
}
.top_counter{
    .icon{        
        @extend .align-center;
        @extend .m-r-10;
        background: $gray-100;
        border-radius: $border-radius-small;
        float: left;
        width: 50px;
        height: 50px;
        
        i{
            font-size: $font-size + 6px;
            line-height: 50px;
        }
    }
    .content{
        height: 50px;        
        .number{
            @extend .m-b-0;
        }
    }
}
.right_chat{
    li {
        position: relative;
        &.offline {
            .status {
                background: $orange;
            }
        }
        &.online {
            .status {
                background: $green;
            }
        }
        &.me {
            .status {
                background: $blue;
            }
        }
        &:last-child{
            .media{
                margin: 0;
            }
        }
    }
    .media {
        .media-object {
            @include transition(all .3s ease-in-out);
            @extend .m-r-10;            
            @include border-radius(4px);
            width: 35px;            
        }
        .name {
            @include display-block;
            font-size: $font-size + 1px;   
            color: $gray-300;
            margin: 0; 
            line-height: 18px;
        }
        .message {
            @include inline-block;
            @extend .font-13;
            color: $gray-400;
        }
        .status {
            @extend .padding-0;
            @include inline-block;            
            @include border-radius(50px);
            min-width: inherit;
            border: 3px solid $body-color;
            position: absolute;
            left: 23px;
            top: 30px;
            width: 11px;
            height: 11px;
        }
        .avtar-pic{
            margin-right: 10px;
        }
    }
}
