.ribbon{
    position: relative;
    .ribbon-box{
        color: $white;
        right: auto;
        left: 0;
        top: 15px;
        padding-left: 12px;
        padding-right: 10px;
        position: absolute;
        height: 32px;
        line-height: 32px;        

        &::before{
            position: absolute;
            display: block;
            width: 0;
            height: 0;
            content: '';
            top: 0;
            right: -8px;
            height: 32px;
            border: 16px solid;
            border-left-width: 0;
            border-right-width: 8px;

        }
        &.green{
            background: $green;
            &::before{
                border-color: $green;
                border-right-color: transparent;
            }
        }
        &.cyan{
            background: $cyan;
            &::before{
                border-color: $cyan;
                border-right-color: transparent;
            }
        }
        &.pink{
            background: $pink;
            &::before{
                border-color: $pink;
                border-right-color: transparent;
            }
        }
        &.orange{
            background: $orange;
            &::before{
                border-color: $orange;
                border-right-color: transparent;
            }
        }
        &.info{
            background: $info;
            &::before{
                border-color: $info;
                border-right-color: transparent;
            }
        }
        &.indigo{
            background: $indigo;
            &::before{
                border-color: $indigo;
                border-right-color: transparent;
            }
        }
    }
}

